@import 'Button.scss';
$green: #4BD865;

.menuPanel {
    width: 20%;
    height: 100%;

    top: 0%;
    left: -20%;

    position: absolute;
    background-color: #112149;

    opacity: 0.9;

    z-index: 11;  

    &-exitButton {
        width: 3%;
        min-width: auto;
        height: 15%;

        bottom: 40%;
        left: 90%; 
        
        background-color: #2A5486;
        border-color: transparent;

        position: absolute;
    }   

    &-toggleSwitch {
        width: 6rem;
        
        height: 3rem;
        display: block;
        position: relative;
        border-radius: 2rem;
        background-color: #fff;
        box-shadow: 0 0 1rem 3px rgba(0 0 0 / 15%);
        cursor: pointer;    
        
        left: 500px;
    }
    &-toggleButton {
        width: 2.6rem;
        height: 2.6rem;
        position: absolute;
        top: 50%;
        left: .2rem;
        transform: translateY(-50%);
        border-radius: 50%;
        background: #f03d3d;
      }
      
     
}

.menuPanelActive {
    @extend .menuPanel;
  
    animation-name: menuAppear;
    animation-delay: 0s;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    animation-direction: normal;  
}

.menuPanelDeactive {
    @extend .menuPanel;

    animation-name: menuAppear;
    animation-delay: 0s;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    animation-direction: reverse;  
}

@keyframes menuAppear {
    0% {
        
    }
    100% {
        left: 0%;
    }
}

.governorInfoPanel {
    width: 38%;
    height: 100%;
    top: 0%;
    right: 0%;

    position: absolute;
    background-color: #ffffff; 

    z-index: 11; 

}

.governorInfoPanelActive {
    @extend .governorInfoPanel;
  
    visibility: visible;
}

.governorInfoPanelDeactive {
    @extend .governorInfoPanel;

    visibility: hidden;
}

.test {
    flex : 1;
    flex-direction: row;
    align-items: flex-start;
}









