.sideMenuButton {
    height: 50px;
    width: 50px;
    
    top: 1%;
    left: 1%;

    position: absolute;

    z-index: 11;

    background-color: transparent;
    border-color: transparent;
    background-image: url("C:/React/digitaltwinMap/map/src/align_justify.png");
    background-size: cover;
    background-repeat: no-repeat;

}

.activeSideMenuButton {
    @extend .sideMenuButton;
    visibility: visible;
    
}

.dectiveSideMenuButton {
    @extend .sideMenuButton;
    visibility: hidden;
}

.governorInfoButton {
