.loginButton {
    box-sizing: border-box;
    appearance: none;
    background-color: transparent;
    border: 2px solid cyan;
    border-radius: 0.6em;
    color: white;
    cursor: pointer;
    display: flex;
    align-self: center;
    font-size: 17px;
    font-weight: 400;
    line-height: 0.1;
    margin: 20px;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700; 
      
  
    &:hover,
    &:focus {
      color: #fff;
      outline: 0;
    }

    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
    &:hover {
      box-shadow: 0 0 40px 40px cyan inset;
    }

    top: 20px;
    left: 15px;
    width: 180px;
    position: relative;
}

$transition: .3s cubic-bezier(0.95, 0.05, 0.795, 0.035);
$width: 80px;
$height: 40px;
$color_checkbox_success:#4cd964;
$color_checkbox_default:#8E8E93;

.toggle{
  
    
    .toggle-control{
      transition: $transition;
      left: 180px;
      bottom: 37px;
       width: $width;
       height: $height;
       display: block;
      border: 2px solid $color_checkbox_default;
      border-radius: $height;
      background-color: rgba(black,.06);
      position: relative;
      
      &:after{
        transition: $transition;
        content: "";
        width: $width/2;
        height: $height;
        display: block;
        background-color: #fff;
        border-radius: 50%;
        box-shadow: 0 1px 2px rgba(black, .4),0 3px 2px rgba(black,.4);
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    
   input{
     display: none;
     &:checked + .toggle-control{
       border-color: $color_checkbox_success;
       background-color: $color_checkbox_success;
       &:after{
         left: $width/2;
       }
     }
   }
   
   .toggle_input{
    
   }
   
 }